
import { defineComponent } from 'vue';
import fComponent from '@/components/util/fComponent.vue';
import {ILResults} from '@/components/models';


export default defineComponent({
  name: 'resultsView',
  components: { 
    fComponent
   },
  data () {
    return { 
      game: 1,
      gamesData: [
        {label: "Loto Real", value: 1}, 
        {label: "Lotería Real", value: 2}, 
        {label: "Nueva Yol Real", value: 3}, 
        {label: "Loto Pool", value: 4},
        {label: "Tu Fecha Real", value: 5},
        {label: "Pega 4", value: 6},
        {label: "La Primera", value: 7},
        {label: "Quiniela Palé", value: 8},
        {label: "Lotería Nacional", value: 9},
        {label: "Gana más", value: 10},
      ],
      
      bolos: ["VE", "RO", "AM", "AZ"],
      acronyms: ["LLR", "NRT", "NNM", "NLP", "NTF", "NP4", "NPR", "NL", "NN", "NNT"],
      NNMApple: ["V", "R", "A"],

      //Results
      resCount: 0,
      currentPage: 1,
      paginatedResult: [] as ILResults[],
      paginated: true,

      //Filter results
      sorteoFilter: '',
      fechaFilter: '',
      tempFechaFilter: '',
      hasFiltered: false,
    };
  },
  methods: {

    async filter(type: string, f: string){

      if(f.length > 0){

        //Clear fields
        if(type == '1'){
          //sorteo
          this.sorteoFilter = '';
        }else if(type == '0'){
          //fecha
          this.fechaFilter = '';
          this.tempFechaFilter = ''; 
        }

        this.paginated = false;
        const res = await this.$api.get("/results/filter", {lottery: this.acronyms[this.game-1], by: type, data: f});

        if(res.success){
          if(res.data ){
                 
            const arr: ILResults[] = [];

            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            res.data.forEach((r: any) => {
              
              const { lottery, date, results, sorteo } = r;
              
              arr.push({
                lottery, 
                results: results.split("-"),
                sorteo,
                date: this.$utils.formatDate(date)
              }) 

            });
            
            //Replace data
            this.paginatedResult = arr;


            setTimeout(() => {
              this.paginated = true;
            }, 300);
          }
        }

        this.hasFiltered = true;
        

      }else if(this.hasFiltered){
        this.hasFiltered = false;
        this.setInitialResults(this.acronyms[this.game-1]);
      }else{
        this.$utils.notify({message: 'Introduzca el número de sorteo'})
      }

    },
    removeFData(){
      this.hasFiltered = false;
      this.sorteoFilter = '';
      this.fechaFilter = '';
      this.tempFechaFilter = '';
    },
    removeFilter(){
      this.removeFData();
      this.setInitialResults(this.acronyms[this.game-1]);
    },



    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    async getLimitedResults(lottery: string, startingRow: number, rowAmount: number): Promise<any> {
        console.log(startingRow)
      this.paginated = false;
        
      const res = await this.$api.get("/results/"+lottery+"/"+startingRow, {rowAmount});
      if(res.success){
        if(res.data && res.data.length > 0){

          const arr: ILResults[] = [];

          if(res.data.length > 0){
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            res.data.forEach((r: any) => {
              
              const { lottery, date, results, sorteo } = r;
              
              arr.push({
                lottery, 
                results: results.split("-"),
                sorteo,
                date: this.$utils.formatDate(date)
              }) 

            });

          }
          setTimeout(() => {
            this.paginated = true;
          }, 300);

          return arr;

        }
      
      }

      return [];

    },
    async setInitialResults(lottery: string, resetPage = true){
     
      const res = await this.$api.get("/results/count/"+lottery)
      if(res.success && res.data.length > 0){
        this.resCount = res.data[0].cRes;
      }
      
      if(resetPage)
      this.currentPage = 1;
        const startingRow = (this.currentPage * 12) - 12;
      this.paginatedResult = await this.getLimitedResults(this.acronyms[this.game-1], startingRow, 12);
      
    },
    getColorRegion(n: number){
      return n > 1 && n <= 25 ? 0 :
             n > 25 && n <= 50 ? 1 :
             n > 50 && n <= 75 ? 2 :
             n > 75 || n == 0 ? 3 : 0;
    },
    getBolo(lottery: string){
      switch(lottery){
        case 'NRT': return 'VE';
        case 'NTF': return 'NA';
        case 'NP4': return 'AM';
        case 'LLR': return 'RO';

        default: return 'BL'
      }
      
    }
  },
  watch:{
    fechaFilter(){
      if(this.fechaFilter){
        if(this.fechaFilter.length > 0){
          const a = this.fechaFilter.split("/");
          this.tempFechaFilter = a[2] + "/" + a[1] + "/" + a[0]; 
          // eslint-disable-next-line @typescript-eslint/no-explicit-any
          (this.$refs['qDateProxy'] as any).hide();

          this.filter('1', this.fechaFilter);

        }
      }else{

        this.tempFechaFilter = '';
        this.hasFiltered = false;
        if(this.sorteoFilter == '')
          this.setInitialResults(this.acronyms[this.game-1], false);

      }
    },
    game(){
      this.removeFilter(); 
    },
    async currentPage(){
        //Get paginated results, inclusive bottom and top
        this.removeFData();
        const startingRow = (this.currentPage * 12) - 12;
        this.paginatedResult = await this.getLimitedResults(this.acronyms[this.game-1], startingRow, 12);
        if(this.$q.screen.lt.md){
          // eslint-disable-next-line @typescript-eslint/no-explicit-any
          const el: any = document.body.querySelector("#pagination");
          if(el)
          el.scrollIntoView({behavior: 'smooth'});
        }
          
    }
  },
  computed:{
    cPageAmount(){
      const res = Math.ceil(this.resCount / 12);
      return res || 1;
    },
  },
  async mounted(){

    this.setInitialResults(this.acronyms[this.game-1]);
  }

});
