
import QForm from 'quasar/src/components/form/QForm.js';;
import {defineComponent} from 'vue';

export default defineComponent({
  name: 'formComponent',
  props: {
    header: {
      type: String,
      required: true
    },
    shade:{
      type: Boolean,
      default: false//true,
    },
    hasFiltered:{
      type: Boolean,
      default: false//true,
    },
  },
  setup () {
    return { 
    };
  },
  methods:{
    
    emitSubmit(){
      this.$emit('submit');
      (this.$refs.qForm as QForm & { resetValidation: () => void }).resetValidation()
    },
    emitRemoveFilter(){
      this.$emit('removeFilter');
    },
  },
  emits: ['submit', 'removeFilter'],

});
